.order-popup__title {
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0em;
  color: #fefefe;
  text-align: center;

  margin: 0;

  padding-bottom: 35px;

  @media (max-width: $mobiles-max) {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;
  }
}

.order-popup {
  width: 100%;
  flex-grow: 1;
  flex-shrink: 0;

  background: #1e87f2;

  padding: 40px 50px 50px 50px;

  border: 3px solid rgba(255, 255, 255, 0.1);
  box-sizing: border-box;
  border-radius: 12px;

  .button--submit {
    margin-top: 70px;

    @media (max-width: $mobiles-max) {
      margin-top: 40px;

    }
  }

  @media (max-width: $mobiles-max) {
    padding: 15px;
  }
}


.order-popup__fields {
  // min-width: 385px;

  @media (max-width: $tablets-max) {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    min-width: auto;

    padding: 15px;
  }
}

.order-popup__fieldset {
  border: none;
  padding: 0;
}

.order-popup__field {
  margin-bottom: 20px;
  width: 100%;
  @media (max-width: $mobiles-max) {
    padding-left: 0;

  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.order-popup__field-wrapper {
  display: flex;
  flex-direction: column;
}

.order-popup__error {
  color: $color-red;
  display: block;
  text-align: right;
  @media (max-width: $mobiles-max) {
    text-align: left;
  }
}

.order-popup__label {
  color: #fefefe;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0px;
  text-align: left;

  margin-bottom: 6px;
}

.order-popup__input {
  width: 100%;
  height: 50px;
  padding: 0 20px;
  border: none;
  transition: $transition-settings;

  background: #fdfdfd;
  border-radius: 4px;

  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
  color: #101a4e;

  @media (max-width: $mobiles-max) {
    max-width: 100%;
  }
  &--filled {
    border: 1px solid $color-black;
  }
  &--error {
    border: 1px solid $color-red;
  }
}
