.form {
  background-image: url("../img/form-background.jpg");
  min-height: 587px;

  padding: 48px 0;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: $mobiles-max) {
    padding: 30px 0;
    min-height: 459px;
    background-image: url("../img/questions-background-mobile.jpg");
  }
}

.form__image {
  width: 49%;
  margin-right: 2%;

  height: 456px;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    background-image: url("../img/arrow-3.svg");
    width: 300px;
    height: 180px;
    bottom: -35px;
    right: 22px;
    background-repeat: no-repeat;
    z-index: 0;


  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    z-index: 1;
    position: relative;
  }

  @media (max-width: $mobiles-max) {
    display: none;
  }
}

.form__title {
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0em;
  color: #fefefe;
  text-align: center;

  margin: 0;

  @media (max-width: $mobiles-max) {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;
  }
}

.form__subtitle {
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: center;
  color: #ededed;

  margin: 15px 0 30px;

  @media (max-width: $mobiles-max) {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: center;

    padding: 0 14px;
    margin: 10px 0 8px;
  }
}

.form__fields {
  width: 49%;
  flex-grow: 1;
  flex-shrink: 0;

  padding: 40px 40px 64px 40px;

  background: #1e87f2;
  border: 3px solid rgba(255, 255, 255, 0.1);
  box-sizing: border-box;
  border-radius: 12px;

  min-width: 385px;

  @media (max-width: $tablets-max) {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    min-width: auto;

    padding: 28px 12px 25px 12px;
  }
}

.form__body {
  display: flex;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    background-image: url(../img/arrow-2.svg);
    right: -260px;
    bottom: -65px;
    width: 260px;
    height: 115px;
    background-repeat: no-repeat;

    @media (max-width: $mobiles-max) {
      display: none;
    }
  }
}

.form__fieldset {
  border: none;
  padding: 0;
}

.form__field {
  margin-bottom: 20px;
  width: 100%;
  @media (max-width: $mobiles-max) {
    padding-left: 0;

  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.form__field-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.form__error {
  color: $color-red;
  display: block;
  text-align: right;
  @media (max-width: $mobiles-max) {
    text-align: left;
  }
}

.form__label {
  color: #fefefe;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0px;
  text-align: left;

  margin-bottom: 6px;
}

.form__input {
  width: 100%;
  height: 50px;
  padding: 0 20px;
  border: none;
  transition: $transition-settings;

  background: #fdfdfd;
  border-radius: 4px;

  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
  color: #101a4e;

  @media (max-width: $mobiles-max) {
    max-width: 100%;
  }
  &--filled {
    border: 1px solid $color-black;
  }
  &--error {
    border: 1px solid $color-red;
  }
}





